.load-more-container {
  /* background: #fff; */
  /* width: 500px;
  min-height: 500px;
  margin: 20px auto;
  position: relative; */
  position: absolute;
  top: 38%;
  left: 9.5%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  min-height: 500px;
  margin: 20px auto;
  min-width: 500px;
}
.load-more-container ul {
  list-style-type: none;
  padding: 0;
}
.load-more-container ul:after {
  content: "";
  display: table;
  clear: both;
}
.load-more-container ul li {
  /* width: calc(25% - 10px); */
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  width: calc(20% - 20px);
  margin: 45px 15px 45px;
  height: 100px;
  /* background: #446CB3; */
  color: #fff;
  float: left;
  padding: 0;
  min-width: 250px;
}
.load-more-container ul li:nth-child(1n + 5) {
  max-height: 0;
  opacity: 0;
  transition: 0.1s ease-in;
}
.load-more-container .load-more-btn {
  width: 150px;
  line-height: 40px;
  border-radius: 5px;
  margin: 0 auto;
  display: block;
  /* background: #219150; */
  background: black;
  color: #fff;
  cursor: pointer;
  text-align: center;
  margin-left: 39%;
  top: 10%;
}
.load-more-container .load-more-btn:hover {
  /* background: green; */
  background: white;
  color: black;
  border: 2px solid black;
}
.load-more-container .load-more-btn .loaded {
  display: none;
}
.load-more-container #load-more {
  display: none;
}
.load-more-container #load-more:checked ~ ul li:nth-child(1n + 5) {
  max-height: 999px;
  opacity: 1;
  transition: 0.2s ease-in;
}
.load-more-container #load-more:checked ~ .load-more-btn .loaded {
  display: block;
}
.load-more-container #load-more:checked ~ .load-more-btn .unloaded {
  display: none;
}

/***********************************/


